.greenBg {
  background-color: #25d366 !important;
}

.intro-banner-vdo-play-btn {
  height: 60px;
  width: 60px;
  border-radius: 100px;
  position: relative; /* Ensure the ripple spans are positioned relative to this container */
}

.intro-banner-vdo-play-btn i {
  font-size: 30px;
}

.intro-banner-vdo-play-btn .ripple {
  position: absolute;
  width: 160px;
  height: 160px;
  z-index: -1;
  left: 50%;
  top: 50%;
  opacity: 0;
  margin: -80px 0 0 -80px;
  border-radius: 100px;
  -webkit-animation: ripple 1.8s infinite;
  animation: ripple 1.8s normal;
}

@-webkit-keyframes ripple {
  0% {
    opacity: 1;
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes ripple {
  0% {
    opacity: 1;
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.intro-banner-vdo-play-btn .ripple:nth-child(2) {
  animation-delay: 0.3s;
  -webkit-animation-delay: 0.3s;
}

.intro-banner-vdo-play-btn .ripple:nth-child(3) {
  animation-delay: 0.6s;
  -webkit-animation-delay: 0.6s;
}

/* Add hover effect */
.intro-banner-vdo-play-btn:hover .ripple {
  -webkit-animation: ripple 1.8s infinite;
  animation: ripple 1.8s infinite;
}

.intro-banner-vdo-play-btn:hover .ripple:nth-child(2) {
  animation-delay: 0.3s;
  -webkit-animation-delay: 0.3s;
}

.intro-banner-vdo-play-btn:hover .ripple:nth-child(3) {
  animation-delay: 0.6s;
  -webkit-animation-delay: 0.6s;
}

.floatingWhatsApp {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 10px;
  right: 10px;
  background-color: #25d366;
  color: #fff;
  border-radius: 50px;
  text-align: center;
  font-size: 30px;
  box-shadow: 2px 2px 3px #999;
  z-index: 100;
  text-align: center;
}

/* Override hover state */
.floatingWhatsApp:hover {
  background-color: #25d366 !important; /* Change hover background color */
  color: #fff !important; /* Ensure text color remains white */
}

.my-float {
  margin-top: 10px;
}
